import React from "react";
import { useRef } from "react";
import { useEffect } from "react";
import { PerspectiveCamera, Scene, Object3D, Vector3 } from "three";
import { TWEEN } from "three/examples/jsm/libs/tween.module.min";
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls";
import {
  CSS3DRenderer,
  CSS3DObject,
} from "three/examples/jsm/renderers/CSS3DRenderer.js";

import table from "../assets/background/background-items";

import "../styles/components/AnimatedBackground.scss";

export default function AnimatedBackground() {
  const mainDiv = useRef(null);

  useEffect(() => {
    const container = mainDiv.current;

    let scene, camera, renderer;
    let controls;

    const objects = [];
    const targets = { sphere: [], helix: [], grid: [] };

    function init() {
      scene = new Scene();
      camera = new PerspectiveCamera(
        60,
        window.innerWidth / window.innerHeight,
        1,
        1000
      );
      camera.position.z = 2000;

      for (let i = 0; i < table.length; i += 1) {
        const element = document.createElement("div");
        element.className = "background-card";
        element.style.backgroundColor =
          "rgba(65,105,225," + (Math.random() * 0.5 + 0.25) + ")";

        const lang = document.createElement("div");
        lang.className = "lang";
        lang.textContent = table[i][0];
        element.appendChild(lang);

        const pre = document.createElement("pre");
        const code = document.createElement("code");
        pre.className = "code";
        code.textContent = table[i][1];
        pre.appendChild(code);
        element.appendChild(pre);

        const objectCSS = new CSS3DObject(element);
        objectCSS.position.x = Math.random() * 4000 - 2000;
        objectCSS.position.y = Math.random() * 4000 - 2000;
        objectCSS.position.z = Math.random() * 4000 - 2000;
        scene.add(objectCSS);

        objects.push(objectCSS);
      }

      // sphere

      const vector = new Vector3();

      for (let i = 0, l = objects.length; i < l; i++) {
        const phi = Math.acos(-1 + (2 * i) / l);
        const theta = Math.sqrt(l * Math.PI) * phi;

        const object = new Object3D();

        object.position.setFromSphericalCoords(800, phi, theta);

        vector.copy(object.position).multiplyScalar(2);

        object.lookAt(vector);

        targets.sphere.push(object);
      }

      // helix

      for (let i = 0, l = objects.length; i < l; i++) {
        const theta = i * 0.35 + Math.PI;
        const y = -(i * 12) + 300;

        const object = new Object3D();

        object.position.setFromCylindricalCoords(900, theta, y);

        vector.x = object.position.x * 2;
        vector.y = object.position.y;
        vector.z = object.position.z * 2;

        object.lookAt(vector);

        targets.helix.push(object);
      }

      // grid

      for (let i = 0; i < objects.length; i++) {
        const object = new Object3D();

        object.position.x = (i % 5) * 400 - 800;
        object.position.y = -(Math.floor(i / 5) % 5) * 400 + 800;
        object.position.z = Math.floor(i / 25) * 1000;

        targets.grid.push(object);
      }

      renderer = new CSS3DRenderer();
      renderer.setSize(window.innerWidth, window.innerHeight);
      container.appendChild(renderer.domElement);

      window.addEventListener("resize", onWindowResize, false);

      controls = new OrbitControls(camera, renderer.domElement);
      controls.minDistance = 500;
      controls.maxDistance = 6000;
      controls.enablePan = false;
      controls.autoRotate = true;
      controls.addEventListener("change", render);

      transform(targets.sphere, 2000);

      animate();
    }

    function transform(targets, duration) {
      TWEEN.removeAll();

      for (let i = 0; i < objects.length; i++) {
        const object = objects[i];
        const target = targets[i];

        new TWEEN.Tween(object.position)
          .to(
            {
              x: target.position.x,
              y: target.position.y,
              z: target.position.z,
            },
            Math.random() * duration + duration
          )
          .easing(TWEEN.Easing.Exponential.InOut)
          .start();

        new TWEEN.Tween(object.rotation)
          .to(
            {
              x: target.rotation.x,
              y: target.rotation.y,
              z: target.rotation.z,
            },
            Math.random() * duration + duration
          )
          .easing(TWEEN.Easing.Exponential.InOut)
          .start();
      }

      new TWEEN.Tween(this)
        .to({}, duration * 2)
        .onUpdate(render)
        .start();
    }

    function onWindowResize() {
      camera.aspect = window.innerWidth / window.innerHeight;
      camera.updateProjectionMatrix();

      renderer.setSize(window.innerWidth, window.innerHeight);

      render();
    }

    function animate() {
      requestAnimationFrame(animate);

      TWEEN.update();

      controls.update();
    }

    function render() {
      renderer.render(scene, camera);
    }

    init();
  }, []);

  return (
    <>
      <div className="background-div" ref={mainDiv}></div>
    </>
  );
}
