import React from "react";
import "../styles/components/FadeInSection.scss";

export default function FadeInSection(props) {
  const divRefs = React.useRef([]);

  React.useEffect(() => {
    const observer = new IntersectionObserver((entries) => {
      entries.forEach((entry) => {
        if (entry.isIntersecting) {
          entry.target.className = "fade-in-section fade-in-section-is-visible";
        } else {
          entry.target.className = "fade-in-section";
        }
      });
    });
    divRefs.current.forEach((currentElement) => {
      observer.observe(currentElement);
    });

    // To ensure that the cleanup function has the correct divRefs.current
    const divRefsCurrentCopy = divRefs.current;

    return () => {
      divRefsCurrentCopy.forEach((currentElement) => {
        observer.unobserve(currentElement);
      });
    };
  }, []);

  return (
    <>
      {Array.isArray(props.children) ? (
        props.children.map((child, index) => {
          return (
            <div key={child.key} ref={(l) => (divRefs.current[index] = l)}>
              {child}
            </div>
          );
        })
      ) : (
        <div ref={(l) => (divRefs.current[0] = l)}>{props.children}</div>
      )}
    </>
  );
}
