import React from "react";
import sanitizeHtml from "sanitize-html";

import "../styles/components/ResumeCard.scss";

export default function ResumeCard(props) {
  const { keyId, template, data } = props;

  const generateHeading1 = (key, elementData) => {
    return (
      <p key={key} className="heading1-p">
        {elementData}
      </p>
    );
  };

  const generateHeading1WithLink = (key, elementData) => {
    return (
      <a key={key} href={elementData[1]} className="heading1-p">
        {elementData[0]}
      </a>
    );
  }

  const generateHeading2 = (key, elementData) => {
    return (
      <p key={key} className="heading2-p">
        {elementData}
      </p>
    );
  };

  const generateStartEndDate = (key, elementData) => {
    return (
      <div key={key} className="start-end-date-div">
        <p className="date-title">Start Date:</p>
        <p className="date-content">{elementData[0]}</p>
        <p className="date-title">End Date:</p>
        <p className="date-content">{elementData[1]}</p>
      </div>
    );
  };

  const generateText = (key, elementData) => {
    return (
      <p key={key} className="text-p">
        {elementData}
      </p>
    );
  };

  const generateHTMLText = (key, elementData) => {
    return (
      <p
        key={key}
        className="text-p"
        dangerouslySetInnerHTML={{
          __html: sanitizeHtml(elementData, {
            allowedTags: ["b", "br"],
          }),
        }}
      />
    );
  };

  const generateTextTitlePair = (key, elementData, title) => {
    return (
      <div className="text-div" key={key}>
        <p className="title">
          {title}
        </p>
        <p className="text">
          {elementData}
        </p>
      </div>
    )
  }

  const generateList = (key, elementData, title = "") => {
    if (elementData && elementData.length > 0) {
      return (
        <React.Fragment key={key}>
          {title && title !== "" && <p className="list-title">{title}</p>}
          <ul className="list-ul">
            {Array.isArray(elementData) && elementData.map((listItem) => {
              return (
                <li className="list-li" key={`${key}-${listItem[0]}`}>
                  {listItem[1]}
                </li>
              );
            })}
          </ul>
        </React.Fragment>
      );
    } else {
      return null;
    }
  };

  const generateHTMLList = (key, elementData) => {
    return (
      <ul key={key} className="list-ul">
        {elementData &&
          Array.isArray(elementData) &&
          elementData.map((listItem) => {
            return (
              <li
                dangerouslySetInnerHTML={{
                  __html: sanitizeHtml(listItem[1], {
                    allowedTags: ["b"],
                  }),
                }}
                className="list-li"
                key={`${key}-${listItem[0]}`}
              />
            );
          })}
      </ul>
    );
  };

  const generateElement = (templateItem, index) => {
    const uniqueKey = `resume-${keyId}-${data[0]}-${templateItem[0]}`;
    const elementData = data[index + 1][0];
    switch (templateItem[1]) {
      case "Heading1":
        return generateHeading1(uniqueKey, elementData);
      case "Heading1WithLink": 
        return generateHeading1WithLink(uniqueKey, elementData);
      case "Heading2":
        return generateHeading2(uniqueKey, elementData);
      case "StartEndDate":
        return generateStartEndDate(uniqueKey, elementData);
      case "Text":
        return generateText(uniqueKey, elementData);
      case "HTMLText":
        return generateHTMLText(uniqueKey, elementData);
      case "TextTitlePair":
        return generateTextTitlePair(uniqueKey, elementData, templateItem[2]);
      case "List":
        return generateList(uniqueKey, elementData, templateItem[2]);
      case "HTMLList":
        return generateHTMLList(uniqueKey, elementData);
      default:
        return <></>;
    }
  };

  return (
    <article className="resume-card-article">
      {template &&
        template.map((templateItem, index) => {
          return generateElement(templateItem, index);
        })}
    </article>
  );
}
